<template>
    <yd-popup v-model="isCouponShow" position="bottom" height="60%">
        <div class="couponLayout">
            <div class="tab">可使用优惠券</div>
            <ul class="ullist">
                <li v-for="(items, index) in item" :key="index" >
                    <div class="center">
                        <yd-flexbox>
                            <yd-flexbox-item class="text-left">
                                <div class="name"><i class="iconfo"></i>{{items.name}}</div>    
                                <p class="time">有效期至{{items.etime}}</p>
                            </yd-flexbox-item>
                            <yd-flexbox-item  class="text-right">
                                <div class="zk">8.9<span>折</span></div>
                                <p class="time">满200可用</p>
                            </yd-flexbox-item>
                        </yd-flexbox>
                        <yd-flexbox class="border-top">
                            <div>
                                <div class="nr">{{items.expression1}}</div>
                            </div>
                            <yd-flexbox-item class="text-right">
                                <yd-button type="danger">立即使用</yd-button>
                            </yd-flexbox-item>
                        </yd-flexbox>

                    </div>
                </li>
            </ul>
            <div class="tabTwo">不可用优惠券</div>
            <ul class="ullist ullistTwo">
                <li v-for="(items, index) in item" :key="index" >
                    <div class="center">
                        <yd-flexbox>
                            <yd-flexbox-item class="text-left">
                                <div class="name"><i class="iconfo"></i>代金券</div>    
                                <p class="time">有效期至2019.06.01</p>
                            </yd-flexbox-item>
                            <yd-flexbox-item  class="text-right">
                                <div class="zk">8.9<span>折</span></div>
                                <p class="time">满200可用</p>
                            </yd-flexbox-item>
                        </yd-flexbox>
                        <yd-flexbox class="border-top">
                            <div>
                                <div class="nr">可与其他优惠活动同时使用，限本手机号。</div>
                            </div>
                            <yd-flexbox-item class="text-right">
                                <yd-button type="danger">立即使用</yd-button>
                            </yd-flexbox-item>
                        </yd-flexbox>

                    </div>
                </li>
            </ul>
        </div>
    </yd-popup>
</template>

<script>
export default {
    data () {
        return {
            isCouponShow: true,
            item:[1,2,3]
        }
    },
    watch: {
        input () {
            this.$emit('msg', this.input)
        }
    },
    mounted () {
        // 获取优惠券列表
        this.$api.couponCouponlist({type: 3}, res => {
            this.item = res.data
        })
    }
}
</script>

<style>
.couponLayout{
    overflow-y: auto;
    height: 60vh;
    position: relative;
}
.ullist {
    padding-top: 1rem;
}
.ullist li .center{ 
    margin:0 .2rem .2rem .2rem; background: #FFF;border-radius: 2px;box-shadow: 0 0 3px #ddd; padding: .2rem; overflow: hidden;position: relative; z-index: 0;
}
.ullist li .center::before,.ullist li .center:first{
    content: ""; border-radius: 20px; width: 15px; height: 15px;
}
.ullist li .center .name{
    font-size: .4rem; color: #333;
}
.ullist li .center .zk{
    font-size: .5rem; color: #E02020; 
}
.ullist li .center .zk span{ font-size: .3rem;}
.ullist li .center .time{ color: #999; font-size: .25rem; height: .8rem; line-height: .8rem;}
.ullist li .center .border-top{ border-top:dotted 1px #ddd; padding-top:.2rem;}
.ullist li .nr{ color: #aaa;}
.tab,.tabTwo{ line-height: .8rem; text-align: left; padding:0 .2rem; color: #999; position: fixed;    z-index: 5;    top: 0;  left: 0; background: #fff; width: 100%;}
.tabTwo{ position: relative;}
.ullistTwo{ padding-top: 0;}
.ullistTwo li .center .name,.ullistTwo li .center .zk,.ullist li .center .time{ color: #D8D8D8;}
</style>



